// import Vue from 'vue'
// import Router from 'vue-router'
import Vue from 'vue'
const Router = require('vue-router')
// import Home from '@/page/home/Home'
// import List from '@/page/list/List'
import common from '@/assets/js/common.js'



Vue.use(Router)


const Home = resolve => {
  import('@/page/home/Home').then(module => {
    resolve(module)
  })
}

const List = resolve => {
  import('@/page/list/List').then(module => {
    resolve(module)
  })
}
 //console.log(common.lanmu())

let typeData = [
"推荐",
"美食",
"旅游",
"时尚",
"健康",
"汽车",
"娱乐",
"猎奇",
"社会",
"财经",
"体育",
"国内",
"家居",
"科技",
"电视",
"NBA",
"人文",
"数码",
]
//console.log(typeData)
let newtypeData =  ['拖动滑块验证','滑动拼图验证','文字点选验证'];
var randomNum1 = Math.floor(Math.random()*newtypeData.length);
var type = newtypeData[randomNum1]

export default new Router({
	routes: [
		{
		path: '/',
		  redirect: '/newhome/'+type+''
		},
		{
		path: '/newhome',
		  redirect: '/newhome/'+type+''
		},
		{
			path: '/home/:id',
			name: 'Home',
			component: Home,
			meta:{keepAlive:true}
		},

		{	
			path: '/newhome/:type',
			name: 'newHome',
			component: resolve => require(['../page/home/newHome.vue'], resolve),
			meta:{keepAlive:true}
		},
		{
			path: '/list',
			name: 'List',
			component: List
		}
	]

})



