<template>
  <header>
    <section class="top-menu">
      <div id="J_top_menu" class="top-menu-list">
        <router-link
          v-for="(item,index) of newList"
          :key="index"
          tag="a"
          @click.native="addClass(index)"
          class="tab-item"
          :to="`/home/${item}`"
        >
          <template v-if="item=='看点'" lang="html">推荐</template>
          <template lang="html" v-else>{{item}}</template>
        </router-link>
      </div>
      <!-- <div class="menu-more">
        <div class="list-shadow"></div>
        <router-link to="/List" class="more-btn" @click.native="sendList" tag="a">
          <a href="javascript:;"></a>
        </router-link>
      </div> -->
    </section>
    <!-- <div class="" @click="getVal()">
			测试 {{ count }}
    </div>-->
  </header>
</template>

<script type="text/javascript">
import bus from "../../assets/bus/bus.js";
export default {
  data() {
    return {
      newList: [],
      scrollDivWidth: ""
    };
  },
  computed: {
    getUserIcons() {
      // return this.$store.state.selectList;
    }
    // count(){
    //   return this.$store.state.count;
    // }
  },
  methods: {
    //测试
    // getVal(event) {
    // 	this.$store.commit('increment')
    // },
    getNewList() {},
    sendList() {
      console.log(this.$route.path);

      // this.$router.push({{path: 'List'},  query: {id: id}})
    },
    addClass(index) {
      console.log(index);
      console.log(61)
      console.log(this.newList)
      document.title = this.newList[index]
      console.log(index)
      this.scrollDivWidth = document
        .querySelector(".top-menu-list a")
        .getBoundingClientRect().width;
      let odivW = this.scrollDivWidth / 2;
      console.log('width'+this.scrollDivWidth);
      let oNavDiv = document.querySelectorAll(".top-menu-list a");
      console.log(oNavDiv.length);
      let oClienWidth = document.body.clientWidth / 2;
      document.querySelector(".top-menu-list").scrollLeft =
        oNavDiv[index].offsetLeft - oClienWidth + odivW;
    }
  },
  mounted() {
    let that = this
    // 顶部数据的请求
    // this.getNewList()
    if (localStorage.getItem("localSelectList")) {
      console.log("有缓存");
      if (localStorage.getItem("List")) {
        console.log("有缓存List");
        // this.$store.commit(
        //   "getSelectList",
        //   JSON.parse(localStorage.getItem("localSelectList"))
        // ); //提交数据到vuex
      } else {
        //没有缓存的话就取十条
        console.log("没有缓存List");
        // this.$store.commit(
        //   "getSelectList",
        //   JSON.parse(localStorage.getItem("localSelectList")).slice(0, 10)
        // ); //提交数据到vuex
      }
      setTimeout(function() {
        var arr = window.location.href.split("/")
        var active = decodeURI(arr[arr.length-1])
        var index = ''
        var aaa = document.querySelectorAll('.top-menu-list>a')
        for(let i=0;i<aaa.length;i++){
          var text = aaa[i].innerText
          if(text=='推荐'){
            index = 0
          }else if(text==active){
            index = i
          }
        }
        console.log(index)
        that.addClass(index)
        document.title = text
      },500)
    } else {
      // console.log(this.$store.state.newList);
      // if (this.$store.state.newList.length > 0) {
      //   this.newList = this.$store.state.newList;
      // } else {
        this.$ajax({
          url: "/a/news.list",
          method: "get"
        }).then(res => {
          this.newList = res.list
          for(var ko = 0; ko < this.newList.length ; ko ++) {
            if(this.newList[ko] == "看点") {
              this.newList[ko] = "推荐"
            }
          }
          // this.$store.state.newList = res.list
          // this.$store.commit("getSelectList", this.newList); //提交数据到vuex
          // localStorage.setItem("localSelectList", JSON.stringify(res.list)); //把前十条的栏目入缓存
          // localStorage.setItem("localSelectLists", JSON.stringify(res.list)); //进入到栏目路由的时候,使用这个全部的数据
          setTimeout(function() {
            var arr = window.location.href.split("/")
            var active = decodeURI(arr[arr.length-1])
            var index = ''
            var aaa = document.querySelectorAll('.top-menu-list>a')
            for(let i=0;i<aaa.length;i++){
              var text = aaa[i].innerText
              if(text=='推荐'){
                index = 0
              }else if(text==active){
                index = i
              }
            }
            console.log(index)
            that.addClass(index)

          },500)
        });
      // }
    }


  },
  watch: {
    getUserIcons(val) {
      // console.log('监听变化：');
       console.log(val);
      this.newList = val;
    }
  }
};
</script>

<style lang="scss" scoped="">
 @import "@/assets/styles/common.scss";
header {
  position: fixed;
  z-index: 100;
  top: 0;
  width: px2rem(375px);
  max-width: 750px;
  height: 35px;
  .top-menu {
    position: relative;
    height: 35px;
    background-color: #f4f5f6;
    // &:after {
    //   content: "";
    //   position: absolute;
    //   bottom: 0;
    //   left: 0;
    //   width: 100%;
    //   height: 1px;
    //   background-image: -webkit-linear-gradient(
    //     top,
    //     transparent,
    //     transparent 40%,
    //     #ddd 0
    //   );
    //   background-size: 100% 1px;
    //   background-position: bottom;
    //   background-repeat: no-repeat;
    // }

    .top-menu-list {
      overflow: hidden;
      overflow-x: scroll;
      // margin-right: 40px;
      white-space: nowrap;
      -webkit-overflow-scrolling: touch;
      a {
        font-size: 14px;
        line-height: 27px;
        display: inline-block;
        height: 30px;
        margin: 5px 0 5px 5px;
        padding: 0 10px;
        -webkit-animation: fadeout 0.4s;
        animation: fadeout 0.4s;
        vertical-align: middle;
        white-space: nowrap;
        color: #999999;
      }
      .router-link-active {
        color: #D4221C;
        font-size: 18px;
        position: relative;
        font-weight: bold;
        // &:after {
        //   content: "";
        //   position: absolute;
        //   bottom: 0;
        //   left: 50%;
        //   width:20px;
        //   height:4px;
        //   background:rgba(212,34,28,1);
        //   border-radius:2px;
        //   height: 1px;
        //   background: #D4221C;
        //   transform: translateX(-50%);
        // }
      }
    }
    .menu-more {
      position: absolute;
      top: 0;
      right: 0;
      width: 40px;
      background-color: #f4f5f6;
      .list-shadow {
        position: absolute;
        left: -10px;
        width: 10px;
        height: 36px;
        background: url(data:image/png;base64,)
          no-repeat 100%;
        background-color: rgba(244, 245, 246, 0.3);
        background-size: contain;
      }
      .more-btn {
        display: block;
        width: 40px;
        height: 36px;
        background-size: 20px;
        background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADwAAAA8AgMAAABHkjHhAAAABGdBTUEAALGPC/xhBQAAAAFzUkdCAK7OHOkAAAAMUExURQAAAPhZWfhZWfhZWVw9SV4AAAADdFJOUwB9HHTmnyoAAAAxSURBVCjPY2CgDhBB49eO8unEFw0FgXowGQLk1/9HgL9Y+BD1/+HqR8NzIPgiVMp3AM6LOHXh3LGaAAAAAElFTkSuQmCC)
          no-repeat 50%;
        background-size: 50%;
      }
    }
  }
}
header .top-menu .top-menu-list::-webkit-scrollbar {
  display: none;
}
</style>



